<template>

    <div ref="box" class="device-size">

        <div class="container">
            <div class="row">
                <div class="col-12 col-md-4">
                    <h3>Browser Info</h3>
                    <ul>
                        <li>Browser CodeName: {{ data.navigator.appCodeName }}</li>
                        <li>Browser Name: {{ data.navigator.appName }}</li>
                        <li>Browser Version: {{ data.navigator.appVersion }}</li>
                        <li>Cookies Enabled: {{ data.navigator.cookieEnabled }}</li>
                        <li>Browser Language: {{ data.navigator.language }}</li>
                        <li>Browser Online: {{ data.navigator.onLine }}</li>
                        <li>Platform: {{ data.navigator.platform }}</li>
                        <li>Browser engine name: {{ data.navigator.product }}</li>
                        <li>User-agent header: {{ data.navigator.userAgent }}</li>
                    </ul>

                    <h3>screen</h3>

                    螢幕解析度的寬 window.screen.width: {{ data.window.screenW }} <br>
                    螢幕解析度的高 window.screen.height: {{ data.window.screenH }} <br>
                    <br>
                    螢幕可用工作區寬度 window.screen.availWidth: {{ data.window.screenAvaiW }} <br>
                    螢幕可用工作區高度 window.screen.availHeight: {{ data.window.screenAvaiH }} <br><br>
                </div>
                <div class="col-12 col-md-4">
                    <h3>window</h3>

                    瀏覽器寬度 window.outerWidth: {{ data.window.outerW }} <br>
                    瀏覽器高度 window.outerHeight: {{ data.window.outerH }} <br>
                    <br>
                    頁面內文寬度 window.innerWidth: {{ data.window.innerW }} <br>
                    頁面內文高度 window.innerHeight: {{ data.window.innerH }} <br><br>
                    
                    <h3>body</h3>

                    網頁可見區域寬 document.body.clientWidth: {{ data.body.clientW }} <br>
                    網頁可見區域高 document.body.clientHeight: {{ data.body.clientH }} <br>
                    <br>
                    網頁正文全文寬 document.body.scrollWidth: {{ data.body.scrollW }} <br>
                    網頁正文全文高 document.body.scrollHeight: {{ data.body.scrollH }} <br>
                    <br>
                    網頁可見區域寬 document.body.offsetWidth: {{ data.body.offsetW }} <br>
                    網頁可見區域高 document.body.offsetHeight: {{ data.body.offsetH }} <br><br>
                </div>
                <div class="col-12 col-md-4">
                    <h3>Box</h3>

                    盒子可見區域寬 藍色區塊.clientWidth: {{ data.element.clientW }} <br>
                    盒子可見區域高 藍色區塊.clientHeight: {{ data.element.clientH }} <br>
                    <br>
                    盒子正文全文寬 藍色區塊.scrollWidth: {{ data.element.scrollW }} <br>
                    盒子正文全文高 藍色區塊.scrollHeight: {{ data.element.scrollH }} <br>
                    <br>
                    盒子可見區域寬 藍色區塊.offsetWidth: {{ data.element.offsetW }} <br>
                    盒子可見區域高 藍色區塊.offsetHeight: {{ data.element.offsetH }} <br>
                </div>
            </div>
        </div>

    </div>

</template>
<script>
import { ref, reactive, onMounted } from 'vue'

export default {
    name: 'DeviceSize',

    setup() {
        // 元素
        let box = ref(null)

        // 數據
        let data = reactive({
            navigator: {
                // appCodeName: null,
                appName: null,
                appVersion: null,
                cookieEnabled: null,
                geolocation: null,
                language: null,
                onLine: null,
                platform: null,
                product: null,
                userAgent: null,
            },
            window: {
                screenW: null,
                screenH: null,
                screenAvaiW: null,
                screenAvaiH: null,
                outerW: null,
                outerH: null,
                innerW: null,
                innerH: null,
            },
            body: {
                clientW: null,
                clientH: null,
                scrollW: null,
                scrollH: null,
                offsetW: null,
                offsetH: null,
            },
            element: {
                clientW: null,
                clientH: null,
                scrollW: null,
                scrollH: null,
                offsetW: null,
                offsetH: null,
            }
        })


        // 方法

        function getValue() {

            // window
            data.window.screenW = window.screen.width;
            data.window.screenH = window.screen.height;
            data.window.screenAvaiW = window.screen.availWidth;
            data.window.screenAvaiH = window.screen.availHeight;
            data.window.outerW = window.outerWidth;
            data.window.outerH = window.outerHeight;
            data.window.innerW = window.innerWidth;
            data.window.innerH = window.innerHeight;

            // document body
            data.body.clientW = document.body.clientWidth;
            data.body.clientH = document.body.clientHeight;
            data.body.scrollW = document.body.scrollWidth;
            data.body.scrollH = document.body.scrollHeight;
            data.body.offsetW = document.body.offsetWidth;
            data.body.offsetH = document.body.offsetHeight;

            // element
            data.element.clientW = box.value.clientWidth;
            data.element.clientH = box.value.clientHeight;
            data.element.scrollW = box.value.scrollWidth;
            data.element.scrollH = box.value.scrollHeight;
            data.element.offsetW = box.value.offsetWidth;
            data.element.offsetH = box.value.offsetHeight;
        }


        onMounted(()=>{
            // device
            data.navigator.appCodeName = window.navigator.appCodeName;
            data.navigator.appName = window.navigator.appName;
            data.navigator.appVersion = window.navigator.appVersion;
            data.navigator.cookieEnabled = window.navigator.cookieEnabled;
            data.navigator.geolocation = window.navigator.geolocation;
            data.navigator.language = window.navigator.language;
            data.navigator.onLine = window.navigator.onLine;
            data.navigator.platform = window.navigator.platform;
            data.navigator.product = window.navigator.product;
            data.navigator.userAgent = window.navigator.userAgent;


            getValue();
        })

        window.addEventListener("resize", function () {
            getValue();
        });



        return {
            data,
            box,
        }
    }
}
</script>

<style lang="scss">

.device-size {
    background-color: skyblue;
    padding: 20px;
}


</style>